html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

blockquote {
  border-left: 4px solid #ccc;
  margin-left: 10px;
  padding: 10px 20px;
}

.turbo-progress-bar {
  background-color: #ec005a;
}

.banner {
  color: #fff;
  background-color: #0058ba;
  border-radius: 3px;
  display: flex;
}

.banner__text {
  flex: auto;
  margin: 0;
  padding: 10px 15px;
}

.banner__close {
  color: #fff;
  flex: none;
  padding: 10px 15px;
}

.banner__close:hover {
  font-weight: bolder;
}

